import React, { useState, useEffect } from "react";

import axios from "axios";
import { useNavigate } from "react-router-dom";
function FrontPage() {
  const navigat = useNavigate();
  const [Name, setName] = useState("");
  const [Number, setNumber] = useState("");
  const [myArray, setMyArray] = useState([]);
  var token = [{}];
  const addNumber = (e) => {
    e.preventDefault();
    myArray.push(Number);
    setNumber("");
  };
  var data;
  const submit = async () => {
    myArray.map((i) => {
      data = parseInt(sessionStorage.getItem("tokenNoGen"));
      token.push({
        number: i,
        tokenNo: data + 1,
      });
      sessionStorage.setItem("tokenNoGen", data + 1);
    });
    token.shift();
    console.log(token);
    await axios
      .post("/api/v1/createUser", { Name, token })
      .then(() => {
        console.log("Data added sucessfully");
        alert("Data added sucessfully");
        setMyArray([]);
        setName("");
        setNumber("");
      })
      .catch((error) => {
        alert("Some error occured please try again");
        console.log(error);
      });
  };
  const clear = () => {
    setMyArray([]);
    setName("");
    setNumber("");
  };
  const userList = () => {
    navigat("/login")
  };
  useEffect(() => {
    if (!sessionStorage.getItem("tokenNoGen")) {
      sessionStorage.setItem("tokenNoGen", 1000);
    }
  }, []);

  return (
    <>
      <div>
        <form>
          <input
            value={Name}
            type="text"
            placeholder="Enter Your Name"
            onChange={(e) => {
              setName(e.target.value);
            }}
          />
          <input
            type="number"
            placeholder="Enter Number"
            value={Number}
            onChange={(e) => {
              setNumber(e.target.value);
            }}
          />
          <button onClick={addNumber}>Add Another Number / Submit</button>
        </form>
      </div>
      <div>
        <p>Your Numbers arev as Follows : </p>
        {myArray.map((i) => (
          <p
            style={{
              display: "inline",
              marginRight: "20px",
            }}
          >
            {i}
          </p>
        ))}
      </div>
      <div>
        <button onClick={submit}>submit</button>
        <button onClick={clear}>clear</button>
        <button onClick={userList}>View users</button>
      </div>
    </>
  );
}

export default FrontPage;
