import React, { useState } from "react";
import Number from "./Number";
import { jsPDF } from "jspdf";

function SingleUser(users) {
  const token = users.users.data;
  const name = users.users.Name;
  var leng=token.length;
  console.log(leng)
  return (
    <div className="userDetails">
      <div className="name">
        <p>Name: </p>
        <p>{users.users.Name}</p>
        <button
            onClick={() => {
              const doc = new jsPDF();
              token&&token.map((i)=>{
                doc.text(`Name :${users.users.Name} `, 10, 10);
                doc.text(`Phone No :${i.number} `, 10, 20);
                doc.text(`Token No :${i.tokenNo}`, 10, 30);
                doc.addPage();
              })
              doc.save(`${users.users.Name}.pdf`);
            }}
          >
            Download PDF
          </button>
      </div>
      <div className="number">
        {token &&
          token.map((i) => (
            <div>
              <Number data={i} nameDetails={name} />
            </div>
          ))}
      </div>
    </div>
  );
}

export default SingleUser;
