import axios from "axios";
import React, { useEffect, useState } from "react";
import SingleUser from "./SingleUser";

function UserList() {
  const [user, setuser] = useState([]);
  const [date, setdate] = useState("");

  const searchByDate = async () => {
    const userData = await axios.post("/api/v1/getUserByDate", { date });
    console.log(userData)
    setuser(userData.data.sucess);
  };

  useEffect(() => {
    async function fetchData() {
      const userData = await axios.get("/api/v1/userData");
      setuser(userData.data.sucess);
    }
    fetchData();
  }, []);

  return (
    <div>
      <h4>Search By Date :</h4>
      <input
        type="date"
        name=""
        id=""
        value={date}
        onChange={(e) => setdate(e.target.value)}
      />
      <button onClick={searchByDate}>serach</button>
      {user &&
        user.map((i) => (
          <div>
            {console.log(i)}
            <SingleUser users={i} />
          </div>
        ))}
    </div>
  );
}

export default UserList;
